import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Platform } from 'ionic-angular';
import { FlurryAnalytics, FlurryAnalyticsObject, FlurryAnalyticsOptions } from '@ionic-native/flurry-analytics';

/*
  Generated class for the StatProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class Stat {
	fa : FlurryAnalyticsObject;

	constructor(public http: HttpClient, public flurryAnalytics: FlurryAnalytics, public plt: Platform) {

	}

	init() {
		let config: FlurryAnalyticsOptions = {
			appKey: 'VGJYH9MN8CP862JPQ9D2',
			reportSessionsOnClose: true,
			enableLogging: true
		};
		if (this.plt.is('android')) {
			config.appKey = 'DZ6XGW24NBJ54CPXXGMN';
		}
		this.fa = this.flurryAnalytics.create(config);
	}

	track(event) {
		this.fa.logEvent(event)
			.then(() => console.log('Logged an event!'))
			.catch(e => console.log('Error logging the event', e));
	}

}



