import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { appSettings } from '../../app/app-settings';
import { Settings } from '../settings/settings';

/*
  Generated class for the TradProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class Trad {

  label: any;
  CAT_LIST: any;

  constructor(public settings: Settings, public translate: TranslateService, public http: HttpClient) {
    this.CAT_LIST = appSettings.CAT_LIST;
    this.label = {};
    //this.init();
  }

  init() {
    let catIdList = [];
    catIdList['fr'] = [134, 138, 137, 135, 140, 141, 142, 136, 139, 133];
    catIdList['en'] = [21690, 21694, 21693, 21691, 21696, 21697, 21698, 21692, 21695, 21689];
    catIdList['es'] = [24793, 24797, 24796, 24794, 24799, 24800, 24801, 24795, 24798, 24792];
    catIdList['pt'] = [24803, 24807, 24806, 24804, 24809, 24810, 24811, 24805, 24808, 24802];
    this.translate.get('GENERAL_OK').subscribe((res: string) => { this.label.GENERAL_OK = res; });
    this.translate.get('GENERAL_ANNULER').subscribe((res: string) => { this.label.GENERAL_ANNULER = res; });
    this.translate.get('GENERAL_ERREUR').subscribe((res: string) => { this.label.GENERAL_ERREUR = res; });

    this.translate.get('MAIN_SIGNALER').subscribe((res: string) => { this.label.MAIN_SIGNALER = res; });
    this.translate.get('MAIN_SIGNALER_1').subscribe((res: string) => { this.label.MAIN_SIGNALER_1 = res; });
    this.translate.get('MAIN_SIGNALER_2').subscribe((res: string) => { this.label.MAIN_SIGNALER_2 = res; });
    this.translate.get('MAIN_SIGNALER_3').subscribe((res: string) => { this.label.MAIN_SIGNALER_3 = res; });

    this.translate.get('MODAL_ADD_HINT_LIST').subscribe((res: string) => { this.label.MODAL_ADD_HINT_LIST = res; });
    this.translate.get('MODAL_ADD_FORM_ERROR').subscribe((res: string) => { this.label.MODAL_ADD_FORM_ERROR = res; });
    this.translate.get('MODAL_ADD_FORM_SEND').subscribe((res: string) => { this.label.MODAL_ADD_FORM_SEND = res; });
    this.translate.get('MODAL_ADD_ANOTHER').subscribe((res: string) => { this.label.MODAL_ADD_ANOTHER = res; });
    this.translate.get('MODAL_ADD_NO_NETWORK').subscribe((res: string) => { this.label.MODAL_ADD_NO_NETWORK = res; });
    this.translate.get('MODAL_ADD_SEARCH_DOUBLE').subscribe((res: string) => { this.label.MODAL_ADD_SEARCH_DOUBLE = res; });
    

    this.translate.get('MAIN_HINT_LIST').subscribe((res: string) => { this.label.MAIN_HINT_LIST = res; });
    this.translate.get('MAIN_URL_SHARE').subscribe((res: string) => { this.label.MAIN_URL_SHARE = res; });

    this.translate.get('MAIN_SHARE_DEBUT').subscribe((res: string) => { this.label.MAIN_SHARE_DEBUT = res; });
    this.translate.get('MAIN_SHARE_FIN').subscribe((res: string) => { this.label.MAIN_SHARE_FIN = res; });
    this.translate.get('MAIN_ATTENTE').subscribe((res: string) => { this.label.MAIN_ATTENTE = res; });

    for (let i = 0; i < this.CAT_LIST.length; i++) {
      this.CAT_LIST[i].id = catIdList[this.settings.allSettings.language][i];
      this.translate.get('CAT_' + i).subscribe((res: string) => {
        this.CAT_LIST[i].label = res;
      });
    }
  }

}



