import 'rxjs/add/operator/toPromise';

import { Injectable } from '@angular/core';

import { Api } from '../api/api';
import { Settings } from '../settings/settings';
import { appSettings } from '../../app/app-settings';
import { Pub } from '../pub/pub';
import { Stat } from '../stat/stat';

import * as Loki from 'lokijs';
import * as LokiIndexedAdapter from 'lokijs/src/loki-indexed-adapter'

@Injectable()
export class Database {
  _user: any;
  db: any;
  jtp: any;
  sessionQ: any;
  lang: any;
  id_max: any;
  asynTaskRunning: any;
  cpt: any;

  constructor(public stat: Stat, public api: Api, public settings: Settings, public pub: Pub) {
    this.asynTaskRunning = false;
    this.cpt = 0;
  }

  calc = item => {
    item.pc1 = Math.round(item.a * 100 / (item.a + item.b));
    item.pc2 = 100 - item.pc1;
    if (item.l > 10) {
      item.pcl = Math.round(item.l * 100 / (item.l + item.u));
    }
    return item;
  };

  loadDB() {
    let promise = new Promise((resolve, reject) => {
      let adapter = new LokiIndexedAdapter();
      this.db = new Loki('JTP.db', {
        autosave: true,
        //autoload: true,
        autosaveInterval: 4 * 1000,
        adapter: adapter
      });

      this.db.loadDatabase({}, (err) => {
        if (err) {
          console.log("error : " + err);
          return false;
        }
        else {
          this.jtp = this.db.getCollection('JTP');
          //return true;
          resolve();
        }
      });
    });
    return promise;

  }

  initDB(lang: any) {
    let promise = new Promise((resolve, reject) => {
      let param = [];
      param['lang'] = lang;
      this.settings.setValue('id_max', 0);
      let seq = this.api.get('initGame', param).share();
      seq.subscribe((res: any) => {
        let jtp = this.db.getCollection('JTP');
        if (jtp) {
          this.db.removeCollection('JTP');
        }
        this.jtp = this.db.addCollection('JTP', { 'unique': 'i' });
        this.jtp.insert(res.items.map(this.calc));
        resolve(true);
      }, err => {
        console.error('ERROR', err);
      });
    });
    return promise;
  }

  startAsynTasks(forceReset = false) {
    this.lang = this.settings.allSettings.language;
    this.id_max = this.settings.allSettings.id_max;
    if (!this.asynTaskRunning || forceReset) {
      this.getMoreQ();
      this.getStat();
    }
    this.asynTaskRunning = true;
  }

  getMoreQ() {
    setTimeout(() => {
      let param = [];
      param['lang'] = this.lang;
      param['id_max'] = this.id_max;
      let seq = this.api.get('moreQ', param).share();
      seq.subscribe((res: any) => {
        //premier appel
        if (res.fi !== "") {
          this.id_max = res.fi;
          this.settings.setValue('id_max', res.fi);
        } else if (res.items.length > 0) {
          for (let i = 0; i < res.items.length; i++) {
            if (!this.jtp.by('i', res.items[i].i)) {
              this.jtp.insert(this.calc(res.items[i]));
            }
          }
          this.id_max = res.items[res.items.length - 1].i;
          this.settings.setValue('id_max', res.items[res.items.length - 1].i);
        }
      }, err => {
        console.error('ERROR', err);
      });
      this.getMoreQ();
    }, 25000);
  }

  getStat() {
    //un refresh au bout de 30s de jeu
    setTimeout(() => {
      let param = [];
      param['lang'] = this.lang;
      let seq = this.api.get('stat', param).share();
      seq.subscribe((res: any) => {
        if (res.items.length > 100) {
          //mise à jour de tous les états à 2
          this.jtp.findAndUpdate({}, function(obj) { obj.e = 2; });
          for (let i = 0; i < res.items.length; i++) {
            let item = this.jtp.by('i', res.items[i].i);
            if (item) {
              item.a = res.items[i].a;
              item.b = res.items[i].b;
              item.l = res.items[i].l;
              item.u = res.items[i].u;
              item.e = res.items[i].e;
              this.jtp.update(this.calc(item));
            }
          }
          // surrpimer tous les état à 2
          this.jtp.findAndRemove({ 'e': 2 });
        }
      }, err => {
        console.error('ERROR', err);
      });
    }, 35000);
  }

  insert(item) {
    this.jtp.insert(item);
  }

  getQ(start, end) {
    return this.sessionQ.slice(start, end);
  }

  capitalizeFirstLetter(string) {
    return string[0].toUpperCase() + string.slice(1);
  };

  createSession(param?) {
    //let trashID = 0;
    //if (!this.settings.allSettings.trash) {
    let random = true;
    let trashID = appSettings.CAT_LIST[2].id;
    //}
    if (param && param.search) {
      this.sessionQ = this.jtp.chain().find({
        '$and': [{
          '$or': [{
            'r': {
              '$containsAny': [param.search.toLowerCase(), param.search.toUpperCase(), this.capitalizeFirstLetter(param.search.toLowerCase())]
            }
          }, {
            's': {
              '$containsAny': [param.search.toLowerCase(), param.search.toUpperCase(), this.capitalizeFirstLetter(param.search.toLowerCase())]
            }
          }]
        }, {
          'c': {
            '$ne': trashID
          }
        }, {
          'answered': {
            '$ne': 1
          }
        }, {
          'answered': {
            '$ne': 2
          }
        }]
      }).data();
    }
    else if (param && param.typePage) {
      //1 : plus aimées
      //2 : plus serrées
      //3 : que j'aime
      //4 : Mes JTP
      this.stat.track(this.settings.allSettings.language + ' typePage ' + param.typePage);
      if (param.typePage === 1) {
        this.sessionQ = this.jtp.chain().find({
          '$and': [{
            'pcl': {
              '$gt': 70
            }
          }, {
            'c': {
              '$ne': trashID
            }
          }, {
            'answered': {
              '$ne': 1
            }
          }, {
            'answered': {
              '$ne': 2
            }
          }]
        }).data();
      }
      else if (param.typePage === 2) {
        this.sessionQ = this.jtp.chain().find({
          '$and': [{
            'pc1': {
              '$between': [40, 60]
            }
          }, {
            'c': {
              '$ne': trashID
            }
          }, {
            'answered': {
              '$ne': 1
            }
          }, {
            'answered': {
              '$ne': 2
            }
          }]
        }).data();
      }
      else if (param.typePage === 3) {
        this.sessionQ = this.jtp.chain().find({
          '$and': [{
            'y': {
              '$eq': true
            }
          }, {
            'c': {
              '$ne': trashID
            }
          }]
        }).data();
      }
      else if (param.typePage === 4) {
        let addArr = this.settings.allSettings.addArr || [];
        /*this.sessionQ = [];
        for (let i = 0 ; i < addArr.length ; i++){
          this.sessionQ.push(this.jtp.by('i',addArr[i]));
        }*/
        random = false;
        //this.sessionQ = this.jtp.chain().find({ 'i': { '$in': addArr } }).simplesort('i').data();
        this.sessionQ = this.jtp.find({ 'i': { '$in': addArr } }).reverse();
      }
    }
    else if (param && param.catId) {
      this.stat.track(this.settings.allSettings.language + ' catId ' + param.catId);
      this.sessionQ = this.jtp.chain().find({
        '$and': [{
          'c': {
            '$eq': param.catId
          }
        }, {
          'answered': {
            '$ne': 1
          }
        }, {
          'answered': {
            '$ne': 2
          }
        }]
      }).data();
    } else {
      this.stat.track(this.settings.allSettings.language + ' random');
      this.sessionQ = this.jtp.chain().find({
        '$and': [{
          'answered': {
            '$ne': 1
          }
        }, {
          'answered': {
            '$ne': 2
          }
        }, {
          'c': {
            '$ne': trashID
          }
        }]
      }).data();
    }
    if (random) {
      this.randomize(this.sessionQ);
    }
  }

  randomize(a) {
    let b, c, d;
    c = a.length;
    while (c) {
      b = Math.random() * c-- | 0;
      d = a[c];
      a[c] = a[b];
      a[b] = d;
    }
  }

  update(item, rep = false) {
    if (rep) {
      this.cpt++;
      if (this.cpt % 30 === 0) {
        this.pub.initPub();
      }
    }
    this.jtp.update(item);
  }

}




